	background: url('ship.jpg');
	background-attachment: fixed;
	background-size:cover;
	margin: 0;
}


#container{
	background-image: linear-gradient(to top, rgba(205, 156, 242, 0.40) 0%, rgba(246, 255, 255, 0.71) 100%);
	box-shadow: 0 15px 30px 1px rgba(128, 128, 128, 0.31);
	text-align: center;
	border-radius: 5px;
	margin: 3em auto;
  background: rgb(0 0 0 / 0.7);
	height: 200px;
	width: 80%;
	padding: 1em;
	
	position: fixed;
	bottom: 0;
	right: 0;
	left: 0;
	top: 0;	
	
	
}

#container h2 {
	background: url('ship.jpg');

    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
/* 	background-image: linear-gradient(to top, #cd9cf2 0%, #f6f3ff 100%); */
	font-family: 'Playfair Display', serif;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size:cover;
	letter-spacing: 2px;
	font-size: 3.5em;
	margin: 0;

	
	
}
#container p {
	font-family: 'Playfair Display', serif;
	margin: 3px 0 0.2em 0;
	font-size: 1.3em;
	color: #7d7d7d;
}

#container input {
/* 	background: rgba(255, 13, 254, 0.90); */
	width: 210px;
	display: inline-block;
	text-align: center;
	border-radius: 7px;
	background: #eee;
	padding: 1em 2em;
	outline: none;
	border: none;
	color: #222;
	transition: 0.3s linear;
}
::placeholder{color: #999;}
#container input:focus {background: rgba(0, 0, 333, 0.10);}

#container button {
  background-image: linear-gradient(to left, #040402 0%, #7d7d7d 100%);
  /* box-shadow: 0 9px 25px -5px #df91fb;
	/*box-shadow: 0 9px 25px -5px #df91fb;*/
	font-family: 'Abel', sans-serif;
	padding: 0.5em 1.9em;
  margin: 2.3em 0 0 0.5em;
  height: 42px;
	border-radius: 7px;
	font-size: 1.4em;
	cursor: pointer;
	color: #FFFFFF;
	font-size: 1em;
	outline: none;
	border: none;
	transition: 0.3s linear;

}
#container button:hover{transform: translatey(2px);}
#container button:active{transform: translatey(5px);}
   